import React, { useState, useEffect } from "react"
import { Section, Heading, Layout, Card, Avatar, Button, ButtonLink } from "components"
import { Trans, t } from "@lingui/macro"
import moment from "moment"
import { Tag, Row, Col, Tooltip, Typography, Badge, Icon, Popover, message } from "antd"
import { mobile, newClient, pricer, PLACEHOLDER_OWL_IMAGE, tablet, shareToSocialMedia } from "helpers"
import { useParams, useHistory } from "react-router"
import { useQuery } from "react-apollo"
import { QUERY_ASSIGNMENT_DETAILS } from "queries/profile"
import { countryMap } from "helpers/countryMapping"
import DynamicIcon from "components/common/DynamicIcon"
import { baseStyles } from "styles/base"
import { Link } from "react-router-dom"
import { country } from "App"
import styled from "styled-components"
import { WINDOW_FEATURES, CUDY_URL, SERVER_BASE_URL } from "helpers/constants"
import { withI18n } from "@lingui/react"

const client = newClient("posting_assignment")

const StyledShareIcon = styled.div`
	&& {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
		width: 30px;
		height: 30px;
		border-radius: 12px;
		background-color: ${({ bg }) => bg};
		cursor: pointer;
		.anticon {
			color: #fff;
			font-size: 16px !important;
		}
	}
`

function AssignmentDetails({ i18n }) {
	const { assignment_id, title } = useParams()
	const [details, setAssignmentDetails] = useState({})
	const { goBack, push } = useHistory()

	const assignmentUrl = `${CUDY_URL}/${country}/assignments/${details.title}--${details.pvid}`

	const theText = i18n._(t`Hey tutor, check out this assignment by ${details.firstNamePosting} ${details.lastNamePosting}. Do you think you fit? Then just apply for it at Cudy. Cudy Marketplace is a marketplace for you
	to find best tutors around Indonesia, Singapore, Malaysia, Philippines, India, Sri Lanka, to teach you anything`)

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const { socialColor: color } = baseStyles

	const handleShare = (url, platform) => {
		if (mobile || tablet) {
			shareToSocialMedia({ title: "Cudy Marketplace", url: assignmentUrl, text: details.title })
			return
		}
		window.open(url, `${platform}-popup`, WINDOW_FEATURES)
	}

	const shareContent = (
		<Row gutter={16} type="flex">
			<Col>
				<StyledShareIcon
					bg={color.FACEBOOK}
					onClick={() =>
						handleShare("https://www.facebook.com/sharer/sharer.php?u=" + assignmentUrl + `&quote=${theText}`, "facebook")
					}
				>
					<DynamicIcon type="icon-facebook-fill" />
				</StyledShareIcon>
			</Col>
			<Col>
				<StyledShareIcon
					bg={color.TWITTER}
					onClick={() => handleShare("https://twitter.com/share?url=" + assignmentUrl + `&text=${theText}`, "twitter")}
				>
					<DynamicIcon type="icon-twitter-fill" />
				</StyledShareIcon>
			</Col>
			<Col>
				<StyledShareIcon
					bg={color.LINKEDIN}
					onClick={() => {
						handleShare(
							`https://www.linkedin.com/shareArticle?mini=true&url=${encodeURIComponent(
								assignmentUrl
							)}&text=${theText}&source=${assignmentUrl}`,
							"linkedin"
						)
					}}
				>
					<DynamicIcon type="icon-linkedin-fill" />
				</StyledShareIcon>
			</Col>
			<Col>
				<StyledShareIcon
					bg={color.WHATSAPP}
					onClick={() => handleShare(`whatsapp://send?text=${theText} ${assignmentUrl}`, "whatsapp")}
				>
					<DynamicIcon type="icon-whatsapp-line" />
				</StyledShareIcon>
			</Col>
			<Col>
				<StyledShareIcon
					bg={color.TELEGRAM}
					onClick={() => {
						handleShare(`https://telegram.me/share/url?url=${encodeURIComponent(assignmentUrl)}&text=${theText}`, "telegram")
					}}
				>
					<DynamicIcon type="icon-telegram-fill" />
				</StyledShareIcon>
			</Col>
		</Row>
	)

	useQuery(QUERY_ASSIGNMENT_DETAILS, {
		client,
		variables: { postingAssignmentPvid: Number(assignment_id) },
		onCompleted: (data) => {
			data = data.detailPostingAssignment
			setAssignmentDetails(data)
		}
	})

	const symbol = (countryMap[userDetails.country] || {}).symbol || (countryMap[details.countryPosting] || {}).symbol

	const friendsWhoJoining =
		(details.profileJoin || []).length === 0 ? (
			<Trans>No friends joining</Trans>
		) : (
			(details.profileJoin || []).map(({ profilePvid, resource, ...item }) => {
				const { firstName, lastName } = item
				return (
					<Tooltip key={profilePvid} title={`${firstName} ${lastName}`}>
						<Avatar key={profilePvid} src={SERVER_BASE_URL + resource.path} />
					</Tooltip>
				)
			})
		)

	const statusColor = details.isActive === 1 ? "#87d068" : "#999"

	useEffect(() => {
		if (userDetails.country !== country) {
			message.error(i18n._(t`This assignment is not available in your country`))
			push(`/${country}/404`)
		}
	}, [i18n, push, userDetails.country])

	return (
		<Layout>
			<Section className="centered" width={mobile ? "100%" : "70%"}>
				<ButtonLink icon="left" className="mb2em" onClick={() => goBack()}>
					&nbsp; <Trans>Back</Trans>
				</ButtonLink>

				<Card autoHeight noHover>
					<Row type="flex" justify="space-between" className="intro-title">
						<Col lg={12}>
							<Trans>Assignment by</Trans> {details.firstNamePosting + " " + (details.lastNamePosting || "")}{" "}
							{mobile ? <br /> : null}
							<span className="datetime grey">{moment(details.createdAt).fromNow()}</span>
						</Col>
						<Col lg={12} className="ta-right">
							<Row gutter={16} type="flex" justify="end">
								<Col lg={6}>
									<Tag color={statusColor}>
										{details.isActive === 1 ? <Trans>Active</Trans> : <Trans>Inactive</Trans>}
									</Tag>
								</Col>
								<Col lg={3}>
									<Popover
										trigger="click"
										content={shareContent}
										title={<Trans>Share this assignment</Trans>}
										placement="bottomRight"
									>
										<Button icon="more" type="ghost" shape="circle" size="small" />
									</Popover>
								</Col>
							</Row>
						</Col>
					</Row>

					<Heading content={details.title} level={4} marginBottom="2em" />

					<Row className="mb1em">
						<Col lg={24}>
							<Section bg="#f9f9f9" className="px1em">
								{(details.acceptanceTutorList || []).length === 0 ? (
									<Typography.Text className="grey">
										<DynamicIcon type="icon-not-found" /> &nbsp;
										<Trans>No tutor accepts the assignment yet</Trans>
									</Typography.Text>
								) : (
									details.acceptanceTutorList.map(({ resource, userPvid, firstName, lastName, ...item }) => {
										const path = resource.path
										const avatar = path ? SERVER_BASE_URL + path : SERVER_BASE_URL + PLACEHOLDER_OWL_IMAGE

										return (
											<Tooltip
												key={userPvid}
												title={
													<span>
														{firstName} {lastName}{" "}
														{item.isAccept ? (
															<Trans>accepts the assignment</Trans>
														) : (
															<Trans>rejects the assignment</Trans>
														)}
													</span>
												}
											>
												<Badge
													count={
														<Icon
															type={item.isAccept ? "check-circle" : "close-circle"}
															theme="filled"
															style={{
																color: item.isAccept ? baseStyles.greenColor.primary : baseStyles.redColor,
																right: 17
															}}
														/>
													}
												>
													<Link to={`/${country}/${item.token}-${userPvid}-${firstName}-${lastName}`}>
														<Avatar src={avatar} className="mr1em" />
													</Link>
												</Badge>
											</Tooltip>
										)
									})
								)}
							</Section>
						</Col>
					</Row>

					<Row gutter={32} className="mb1em">
						<Col lg={8}>
							<Heading reverse content={<Trans>Subject</Trans>} subheader={details.programmeSubjectName} />
						</Col>
						<Col lg={8}>
							<Heading reverse content={<Trans>Curriculum</Trans>} subheader={details.programmeLevelName} />
						</Col>
						<Col lg={8}>
							<Heading
								reverse
								content={<Trans>Preferred tutor experience</Trans>}
								subheader={<Trans>{details.tutorExperience} years</Trans>}
							/>
						</Col>
					</Row>

					<Row gutter={32} className="mb1em">
						<Col lg={8}>
							<Heading reverse content={<Trans>Preferred gender</Trans>} subheader={details.tutorGender} />
						</Col>
						<Col lg={8}>
							<Heading reverse content={<Trans>Location</Trans>} subheader={details.location} />
						</Col>
						<Col lg={8}>
							<Heading reverse content={<Trans>Your friends</Trans>} subheader={friendsWhoJoining} />
						</Col>
					</Row>

					<Row gutter={32}>
						<Col lg={8}>
							<Heading
								reverse
								content={<Trans>Price range</Trans>}
								subheader={`${symbol}${pricer(details.priceRangeStartPrice)} - ${symbol}${pricer(
									details.priceRangeEndPrice
								)}`}
							/>
						</Col>
						<Col lg={8}>
							<Heading reverse content={<Trans>Additional notes</Trans>} subheader={details.notes || "-"} />
						</Col>
					</Row>
				</Card>
			</Section>
		</Layout>
	)
}

export default withI18n()(AssignmentDetails)
